'use client';
import { useState } from 'react';

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <nav className="bg-gray-800 text-white shadow-md">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16">
          {/* Logo */}
          <div className="flex items-center">
            <span className="font-bold text-2xl">Ana Paula Barros</span>
          </div>

          {/* Links Desktop */}
          <div className="hidden md:flex space-x-8">
            <a
              href="#sobre"
              className="px-3 py-2 rounded border border-transparent hover:border-blue-400 hover:bg-blue-900 transition-colors duration-300"
            >
              Sobre
            </a>
            <a
              href="#projetos"
              className="px-3 py-2 rounded border border-transparent hover:border-blue-400 hover:bg-blue-900 transition-colors duration-300"
            >
              Projetos
            </a>
            <a
              href="#contato"
              className="px-3 py-2 rounded border border-transparent hover:border-blue-400 hover:bg-blue-900 transition-colors duration-300"
            >
              Contato
            </a>
          </div>

          {/* Botão Mobile */}
          <div className="md:hidden">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="text-gray-300 hover:text-white focus:outline-none"
              aria-label="Toggle menu"
            >
              {isOpen ? (
                // Ícone X
                <svg
                  className="h-6 w-6"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M6 18L18 6M6 6l12 12"
                  />
                </svg>
              ) : (
                // Ícone hambúrguer
                <svg
                  className="h-6 w-6"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M4 6h16M4 12h16M4 18h16"
                  />
                </svg>
              )}
            </button>
          </div>
        </div>

        {/* Menu Mobile */}
        {isOpen && (
          <div className="md:hidden">
            <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3">
              <a
                href="#sobre"
                className="block px-3 py-2 rounded-md border border-transparent hover:border-blue-400 hover:bg-blue-900 transition-colors duration-300 text-base"
              >
                Sobre
              </a>
              <a
                href="#projetos"
                className="block px-3 py-2 rounded-md border border-transparent hover:border-blue-400 hover:bg-blue-900 transition-colors duration-300 text-base"
              >
                Projetos
              </a>
              <a
                href="#contato"
                className="block px-3 py-2 rounded-md border border-transparent hover:border-blue-400 hover:bg-blue-900 transition-colors duration-300 text-base"
              >
                Contato
              </a>
            </div>
          </div>
        )}
      </div>
    </nav>
  );
}
